body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.video-viewer {
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 10;
    background: black;
}

.video-viewer:-webkit-full-screen .video-compare-container {
    height: 100%;
    width: 100%;
}

.video-viewer:-webkit-full-screen video {
    height: auto;
    width: 100vw;
}

.hotkeys-div {
    height: 100%;
    width: 100%;
}

.offset-indicator {
    opacity: 0.4;
    z-index: 14;
    height: 20px;
    top: 25px;
    right: 2px;
    position: absolute;
    background: white;
}

.left-source-selector {
    position: absolute;
    top: 2px;
    left: 2px;
    width: calc(50% - 50px);
}

.right-source-selector {
    position: absolute;
    top: 2px;
    right: 2px;
    width: calc(50% - 50px);
}

video::-webkit-media-controls-enclosure {
    z-index: 14;
}

.close-viewer {
    background: lightgrey;
    position: absolute;
    display: inline-block;
    font-size: 16px;
    top: -10px;
    right: -10px;
    z-index: inherit;
    cursor: pointer;
}

.big-play-button {
    position: absolute;
    display: -webkit-flex;
    display: flex;
    color: white;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    top: 50%;
    left: 50%;
    margin-left: -32px;
    margin-top: -32px;
    border-radius: 32px;
    width: 64px;
    height: 64px;
    line-height: 32px;
    opacity: 0.8;
    padding: 0;
    background: black;
    border: solid 2px white;
    z-index: 15;
}

.big-play-button .hidden {
    display: none;
}

.controls {
    display: -webkit-flex;
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    background-color: rgba(0, 0, 0, 0);
    color: #eee;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 1em;
    border-radius: 1em;
    text-align: center;
    opacity: 0;
    transition: opacity .5s ease-in-out;
    transition-delay: 0;
    z-index: 14;
    width: 95%;
    height: 40px;
}

.controls > button {
    margin-left: 2px;
    margin-right: 2px;
    border-radius: 16px;
    width: 32px;
    height: 32px;
    line-height: 16px;
    opacity: 0.8;
    padding: 0;
    background: black;
    border: solid 2px white;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
}

.controls svg {
    height: 18px;
    width: 18px;
    color: white;
    fill: white;
}

input[type="range"] {
    width: 100%;
    -webkit-appearance: none;
    background: transparent;
    margin-left: 2px;
    margin-right: 2px;
    margin-bottom: 4px;
}

input[type="range"]::-webkit-slider-thumb {
    border: 2px solid white;
    box-shadow: 0px 10px 10px rgba(0,0,0,0.25);
    height: 22px;
    width: 22px;
    border-radius: 11px;
    background: black;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -9px;
}

input[type=range]::-moz-range-thumb {
    border: 2px solid white;
    box-shadow: 0px 10px 10px rgba(0,0,0,0.25);
    height: 22px;
    width: 22px;
    border-radius: 11px;
    background: black;
    cursor: pointer;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    border: 2px solid white;
    border-radius: 4px;
    background: black;
}

input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    border: 2px solid white;
    border-radius: 4px;
    background: black;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: black;
}


.controls > button:before {
    position: relative;
    left: -3px;
}

.controls:hover {
    pointer-events: auto;
    opacity: 1;
}

.filler {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
}

.time-display {
    color: white;
    position: absolute;
    left: calc(50% - 45px);
    top: 4px;
    width: 90px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    z-index: 14;
}

.source-selector {
    opacity: 0.4;
    z-index: 14;
    height: 24px;
    position: absolute;
    background: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    overflow: hidden;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-radius: 6px;
}


.spacer {
    width: 10px;
    display: inline-block;
}

.source-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
}

.url-button {
    width: 20px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-flex-grow: 0;
            flex-grow: 0;
}

.url-input {
    height: 24px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.hidden {
    display: none;
}

.source-file-input {
    font-size: medium;
    height: 20px;
    display: inline-block;
    cursor: pointer;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-flex-grow: 0;
            flex-grow: 0;
}

.source-file-input > input[type="file"] {
    display: none;
}

.source-selector:hover {
    opacity: 1.0;
    z-index: 14;
}

.source-selector select {
    border-radius: 5px;
    margin-right: 2px;
    background: white;
    outline: none;
    /*border-color: #0000aa;*/
}

.source-selector:hover select {
    background: white;
}

.source-metadata {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 30%;
}

.source-name {
    height: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
}
.split-view-container {
    display: block;
    line-height: 0;
    position: relative;
    height: 100%;
    width: 100%;
    z-index: inherit;
    outline: none;
}

.split-view-container:-webkit-full-screen {
    width: 100%;
    height: 100%;
}

.split-view-container > video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: auto;
}

.split-view-container:-webkit-full-screen > video {
    width: 100%;
    height: 100%;
}

.split-view-container:-webkit-full-screen video {
    width: 100vw;
    height: 100vh;
}

.split-view-clipper {
    width: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
    overflow: hidden;
    z-index: 11;
}

.split-view-clipper.show-border {
    border-right: solid black 1px;
}

.split-view-clipper video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: auto;
}

.split-view-container:-webkit-full-screen .split-view-clipper {
    height: 100%;
}

.split-view-container:-webkit-full-screen .split-view-clipper video {
    height: 100%;
}

.dragging {
    cursor: -webkit-grab;
    cursor: grab;
}

.help {
    background: white;

    width: 600px;
    height: 600px;
    z-index: 100;
    overflow-y: auto;

    position: absolute;
    top: 50%;
    left: 50%;

    margin: -300px 0 0 -300px;
    opacity: 0.5;

    border-radius: 10px;
}

.help:hover {
    opacity: 1.0;
}

.help-header {
    height: 24px;
}

.help .hidden {
    display: none;
}

.close-button {
    position: absolute;
    top: 0px;
    right: 2px;
    cursor: pointer;
}

.help-button {
    width: 24px;
    height: 24px;
    z-index: 100;
    position: absolute;
    right: 10px;
    bottom: 10px;
    background: white;
    opacity: 0.5;
    cursor: pointer;
    border-radius: 12px;
}

.help-button:hover {
    opacity: 1.0;
}
